.hero {
	margin-top: 4rem;
	padding: 0;
	padding-left: 4rem;
	background-image: url(/static/media/hero-image.4ee7d836.jpg);
	background-color: var(--medium-blue);
	background-size: cover;
	/*background-repeat: no-repeat;*/
	min-height: 80vh;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	font-size: calc(10px + 2vmin);
	color: var(--dark-blue);
}

.hero h1 {
	margin-top: 8rem;
	font-size: 3.5rem;
	font-weight: 600;
	text-shadow: 2px 2px var(--charcoal-color);
}

.hero p {
	padding: 0;
	text-shadow: 1px 1px black;
}

.hero button {
	margin-top: 3rem;
	padding: 0.5rem 1rem;
	font-size: 2rem;
	font-weight: 600;
	border: 3px solid var(--dark-blue );
	border-radius: 1.5rem;
	background-color: white;
}

.hero button:focus {
	outline: none;
}

.hero a {
	text-decoration: none;
	color: var(--dark-blue);
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
.toggle-btn {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	height: 50px;
	width: 30px;
	background: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
	box-sizing: border-box;
	color: white;
	font-size: 2rem;
}

.toggle-btn:focus {
	outline: none;
}

.toggle-btn-line {
	width: 30px;
	height: 2px;
	background: white;
}
/* Toolbar.css */

.navbar {
	top: 0;
	left: 0;
	width: 100%;
	height: 56px;
	background: var(--dark-blue);
	box-shadow:         2px 2px 4px var(--charcoal-color);
	z-index:999;
}

.navbar-navigation {
	display: flex;
	align-items: center;
	height: 100%;
	padding: 0 1rem;
}

.navbar-logo a {
	padding-left: 1rem;
	color: white;
	text-decoration: none;
	font-size: 2rem;
}

.spacer {
	/* This takes all available space between
	the logo and the navigation items */
	flex: 1 1;
}

.navbar-navigation-items ul {
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
}

.navbar-navigation-items li {
	padding: 0 0.5rem;
}

.navbar-navigation-items a {
	color: white;
	text-decoration: none;
}

.navbar-navigation-items a:hover,
.navbar-navigation-items a:active {
	color: darkorange;
}

@media (max-width: 768px) {
	.navbar-navigation-items {
		display: none;
	}
}

@media (min-width: 769px) {
	.navbar-toggle-btn {
		display: none;
	}

	.navbar-logo {
		margin-left: 0;
	}
}

.side-drawer {
	height: 100%;
	background-color: white;
	box-shadow: 1px 0 7px rgba(0,0,0,0.5);
	position: fixed;
	top: 0;
	left: 0;
	width: 70%;
	max-width: 400px;
	z-index: 200;
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
	-webkit-transition: -webkit-transform 250ms ease-out;
	transition: -webkit-transform 250ms ease-out;
	transition: transform 250ms ease-out;
	transition: transform 250ms ease-out, -webkit-transform 250ms ease-out;
}

.side-drawer.open {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
}

.side-drawer ul {
	height: 100%;
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.side-drawer li {
	margin: 0.5rem 0;
}

.side-drawer a {
	color: #521751;
	text-decoration: none;
	font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
	color: #fa923f;
}

@media (min-width: 769px) {
	.side-drawer {
		display: none;
	}
}
